import { useDispatch, useSelector } from 'react-redux';

import { uiOpenModal } from '../../actions/ui';
import searchIcon from '../../assets/searchIcon.svg';

import './facturas.css';

const InvoiceClientCard = (props) => {

    const dispatch = useDispatch();

    const { blockCharge } = props;

    const { activeInvoice } = useSelector( state => state.invoice );

    const handleSearchClientModal = () => {
        dispatch( uiOpenModal() );
    }
    
    return (
        <div className='invoice-client-card'>
            <div className='select-client'>
                <p className='select-client-title'>Cliente</p>
                <div
                    className={ ( blockCharge ) 
                                ? 'search-client-button disabled-button-action' 
                                : 'search-client-button' 
                            }
                    onClick={ !blockCharge ? handleSearchClientModal : undefined }
                >
                    <img src={searchIcon} alt='search' />
                </div>
            </div>

            <div className='info-client'>
                <div className='info-client-row'>
                    <p className='info-client-title-card two-lines-card'>Código: </p>
                    {( activeInvoice[0] && activeInvoice[0].cliente )
                        ? <p className='info-client-detail-card'>{activeInvoice[0].cliente.trim()}</p>
                        : <p className='info-client-detail-card'>-</p>
                    }
                </div>

                <div className='info-client-row'>
                    <p className='info-client-title-card two-lines-card'>RUC: </p>
                    {( activeInvoice[0] && activeInvoice[0].ruc )
                        ? <p className='info-client-detail-card'>{activeInvoice[0].ruc.trim()}</p>
                        : <p className='info-client-detail-card'>-</p>
                    }
                </div>

                <div className='info-client-column'>
                    <p className='info-client-title-card'>Nombre: </p>
                    {( activeInvoice[0] && activeInvoice[0].nombrec )
                        ? <p className='info-client-detail-card'>{activeInvoice[0].nombrec.trim()}</p>
                        : <p className='info-client-detail-card'>-</p>
                    }
                </div>

                <div className='info-client-column'>
                    <p className='info-client-title-card'>Dirección: </p>
                    {( activeInvoice[0] && activeInvoice[0].direccion 
                        && activeInvoice[0].direccion.trim().length > 0 )
                            ? <p className='info-client-detail-card'>{activeInvoice[0].direccion.trim()}</p>
                            : <p className='info-client-detail-card'>-</p>
                    }
                </div>

                <div className='info-client-column'>
                    <p className='info-client-title-card'>Teléfono: </p>
                    {( activeInvoice[0] && activeInvoice[0].telefono 
                        && activeInvoice[0].telefono.trim().length > 0 )
                            ? <p className='info-client-detail-card'>{activeInvoice[0].telefono.trim()}</p>
                            : <p className='info-client-detail-card'>-</p>
                    }
                </div>

                <div className='info-client-column'>
                    <p className='info-client-title-card'>Correo: </p>
                    {( activeInvoice[0] && activeInvoice[0].email 
                        && activeInvoice[0].email.trim().length > 0 )
                            ? <p className='info-client-detail-card'>{activeInvoice[0].email.trim()}</p>
                            : <p className='info-client-detail-card'>-</p>
                    }
                </div>
            </div>
        </div>
    )
}

export default InvoiceClientCard;